import { scroller } from "react-scroll";
import { NavHashLink } from "react-router-hash-link";
import { Link } from "react-router-dom";

const scrollOpt = {
    isDynamic: true,
    offset: -80,
    spy: true,
    smooth: true,
    duration: 500,
};

class Menu extends React.Component {
    shouldComponentUpdate(nextProps) {
        if (nextProps.menuItems.length !== this.props.menuItems.length) {
            return true;
        }

        return false;
    }

    renderMenuItem = (item, i) => (
        <NavHashLink
            key={i}
            to={`/#${item.alias}`}
            // smooth
            activeClassName="menu__item_active"
            className={`menu__item ${item.cssClass}`}
            // scroll={el => el.scrollIntoView({ behavior: "smooth", block: "start", })}
            scroll={(el) => {
                const elName = el.getAttribute("name");
                scroller.scrollTo(elName, scrollOpt);
            }}
        >
            {item.title}
        </NavHashLink>

        // <Link
        //     key={i}
        //     onClick={() => {
        //         if (item.onClick) {
        //             item.onClick();
        //         } else {
        //             this.props.menuItemClick(item.alias);
        //             // scroller.scrollTo(`section-${item.alias}`);
        //         }
        //     }}
        //     activeClass="menu__item_active"
        //     className={"menu__item" + (item.cssClass ? (" " + item.cssClass) : "")}
        //     to={`section-${item.alias}`}
        //     isDynamic
        //     offset={-80}
        //     spy
        //     smooth
        //     duration={500}
        // >
        //     {item.title}
        // </Link>

        // <a
        //     // href={`/#section-${item.alias}`}
        //     href="/"
        //     key={i}
        //     className={"menu__item" + (item.cssClass ? (" " + item.cssClass) : "")}

        // >{item.title}
        // </a>
    )

    renderLink = item => <Link key={item.link} to={item.link} className="menu__item">{item.title}</Link>

    render() {
        const { menuItems, } = this.props;
        if (!menuItems.length) return null;
        return (
            <nav className="menu">
                {menuItems.map((item, i) => (
                    item.link
                        ? this.renderLink(item, i)
                        : this.renderMenuItem(item, i, menuItems[i].active)
                ))}
            </nav>
        );
    }
}
Menu.propTypes = { menuItems: PropTypes.instanceOf(Array), };
Menu.defaultProps = { menuItems: [], };
export default Menu;
