import { reactLocalStorage } from "reactjs-localstorage";
import { ADD_TO_CART, CLEAR_CART, REMOVE_FROM_CART, REMOVE_ALL_ITEMS_FROM_CART, LOAD_CART } from "../AC";
import initState from "../store/initState";
import { fbPixel } from "../helpers";

function cartReducer(cart = initState.cart, { type, item, items, id, }) {
    switch (type) {
    case ADD_TO_CART: {
        const newCart = [...cart];
        const findId = newCart.findIndex(cartItem => cartItem.itemData.id === item.id);
        if (findId > -1) {
            newCart[findId].numberItems += 1;
        } else {
            newCart.push({
                inCart: true,
                itemData: {
                    ...item,
                    type: "product",
                },
                numberItems: 1,
            });
        }
        fbPixel("micro");
        reactLocalStorage.setObject("cart", { items: newCart, });
        return newCart;
    }

    case REMOVE_FROM_CART: {
        const newCart = [...cart];
        const findId = newCart.findIndex(cartItem => cartItem.itemData.id === id);
        if (newCart[findId].numberItems > 1) {
            newCart[findId].numberItems -= 1;
        } else {
            newCart.splice(findId, 1);
        }
        reactLocalStorage.setObject("cart", { items: newCart, });

        return newCart;
    }

    case REMOVE_ALL_ITEMS_FROM_CART: {
        const newCart = [...cart];
        const findId = newCart.findIndex(cartItem => cartItem.itemData.id === id);
        newCart.splice(findId, 1);

        reactLocalStorage.setObject("cart", { items: newCart, });

        return newCart;
    }

    case LOAD_CART: {
        const newCart = items.map(el => getCartItem(el));

        reactLocalStorage.setObject("cart", { items: newCart, });

        return newCart;
    }

    case CLEAR_CART: {
        reactLocalStorage.setObject("cart", { items: [], });
        return [];
    }

    default: {
        return cart;
    }
    }
}

function getCartItem(item) {
    return {
        inCart: true,
        numberItems: item.quantity ? item.quantity : 1,
        itemData: {
            ...item,
            type: "product",
        },
    };
}
export default cartReducer;
