import React from "react";
import classNames from "classnames";
import { YMInitializer } from "react-yandex-metrika";
import ReactPixel from "react-facebook-pixel";
import Menu from "./Menu";
import InstagramIcon from "./icons/InstagramIcon";
import FacebookIcon from "./icons/FacebookIcon";
import { fbPixel } from "../helpers";

class Footer extends React.PureComponent {
    componentDidMount() {
        const options = {
            autoConfig: true,
            // debug: true,
        };
        ReactPixel.init("320228355308484", null, options);
    }

    sendPixel = () => {
        fbPixel("micro");
    }

    render() {
        const menuItems = [
            {
                title: "О нас",
                onClick: () => this.props.onNavigateRequest("#/about", true),
            },
            {
                title: "Акции",
                onClick: () => this.props.onNavigateRequest("#/sales", true),
            },
            {
                title: "Рестораны",
                onClick: () => this.props.onNavigateRequest("#/restaurants", true),
            }
        ];

        if (!this.props.showMobileAuth) {
            menuItems.push({
                link: "#/legal",
                title: "Правовая информация",
            });
        }
        return (
            <footer className={classNames("footer", { "mobile-page": this.props.showMobileAuth, })}>
                <div className="container">
                    <div className="footer__content">
                        <div className="top-level">
                            <div className="left-bar">
                                <Menu menuItems={menuItems} />
                            </div>
                            <div className="right-bar">
                                <div className="contact-number">
                                    <a
                                        href={`tel:${this.props.companyPhone}`}
                                        onClick={this.sendPixel}
                                    >
                                        {this.props.companyPhone}
                                    </a>
                                </div>
                            </div>
                        </div>
                        <div className="bottom-level">
                            <div className="left-bar">
                                <div className="text-description copyright">2019 pobo. Все права защищены</div>
                            </div>
                            <div className="right-bar">
                                <div className="social-icons">
                                    <a
                                        href={this.props.socialLinks.instagram}
                                        className="social-icons__item"
                                        rel="noopener noreferrer"
                                        target="_blank"
                                        onClick={this.sendPixel}
                                    >
                                        <InstagramIcon />
                                    </a>
                                    <a
                                        href={this.props.socialLinks.facebook}
                                        className="social-icons__item"
                                        rel="noopener noreferrer"
                                        target="_blank"
                                        onClick={this.sendPixel}
                                    >
                                        <FacebookIcon />
                                    </a>
                                </div>
                                <div className="tag">#{this.props.socialLinks.hashtag}</div>
                            </div>
                        </div>
                    </div>
                </div>
                <YMInitializer accounts={[54294081]} options={{ webvisor: true, }} version="2" />
            </footer>
        );
    }
}
Footer.propTypes = {
    showMobileAuth: PropTypes.bool.isRequired,
    onNavigateRequest: PropTypes.func.isRequired,
    companyPhone: PropTypes.string.isRequired,
    socialLinks: PropTypes.shape({
        hashtag: PropTypes.string,
        facebook: PropTypes.string,
        instagram: PropTypes.string,
    }).isRequired,
};
export default Footer;
