import React from "react";
import classNames from "classnames";
import Helmet from "react-helmet";
import { fbPixel, getPrice1, setDeliveryMethod, formatCurrency, getBoulTitle } from "../../../helpers";
import AddIcon from "../../icons/AddItem";
import RemoveIcon from "../../icons/RemoveItem";
import RemoveItemButton from "../../icons/RemoveItemButton";
import EmptyBasketIcon from "../../icons/EmptyBasketIcon";
import Hits from "../../Hits";
import warnIcon from "./warning-icon.svg";

class BasketArea extends React.Component {
    get isDeliveryAvailable() {
        const { min_amount_for_delivery, cart, } = this.props;
        const cartPrice = getPrice1(cart);
        return (cartPrice === 0 || min_amount_for_delivery === 0 || cartPrice >= min_amount_for_delivery);
    }

    get basketItems() {
        const { cart, removeAllItemsFromCart, removeFromCart, addToCart, } = this.props;
        // console.log(cart);
        return cart.map(item => (
            <li key={item.itemData.title} className="basket__item basket-pos js-basket-pos">
                <div className="basket-pos__header">
                    <div className="basket-pos__title">{item.itemData.title}</div>
                    <div className="basket-pos__price">
                        <span data-price={item.itemData.price}>{formatCurrency(item.itemData.price || item.itemData.amount)}</span>
                    </div>
                    {/* <div className="basket-pos__consist">{item.itemData.description ? item.itemData.description : getBoulTitle(item.itemData.groups)}</div> */}
                    <div className="basket-pos__consist">{item.itemData.description}</div>
                    <div className="basket-pos__specifications">{item.itemData.specifications && item.itemData.specifications.map(prop => (
                        <div
                            key={prop.title}
                            className="basket-pos__prop"
                        >
                            {`${prop.value} ${prop.title}`}
                        </div>
                    ))}
                    </div>
                </div>
                <div className="basket-pos__footer">
                    <div
                        className="basket-pos__delete"
                        onClick={() => {
                            removeAllItemsFromCart(item.itemData.id);
                        }}
                    >
                        <RemoveItemButton />
                    </div>
                    <div className="basket-pos__amount amount-item">
                        <div
                            onClick={() => {
                                removeFromCart(item.itemData.id);
                            }}
                            className="amount-item__dec"
                        ><RemoveIcon />
                        </div>
                        <span className="amount-item__field">{item.numberItems}</span>
                        <div
                            onClick={() => {
                                addToCart(item.itemData);
                            }}
                            className="amount-item__inc"
                        ><AddIcon />
                        </div>
                    </div>
                </div>
            </li>
        ));
    }

    get basket() {
        return (
            <div className="basket-total__prices">
                <p className="basket-total__title">Стоимость заказа</p>
                <p className="basket-total__cost">
                    <span className="js-total-cost">{formatCurrency(getPrice1(this.props.cart))}</span>
                </p>
            </div>
        );
    }

    get buttons() {
        return (
            <div className="basket-total__buttons">
                <button
                    className="basket-total__btn_dark btn-transparent"
                    onClick={() => {
                        this.redirectToPayment(1);
                        fbPixel("micro");
                    }}
                    type="button"
                >Самовывоз
                </button>
                <button
                    className="basket-total__btn_light btn-primary"
                    disabled={!this.isDeliveryAvailable}
                    onClick={() => {
                        this.redirectToPayment(2);
                        fbPixel("micro");
                    }}
                    type="button"
                >Доставка
                </button>
            </div>
        );
    }

    get items() {
        if (this.props.cart.length) {
            return (
                <ul className="basket__list">{this.basketItems}</ul>
            );
        }
        return (
            <div className="basket-empty">
                <div className="basket-empty__icon"><EmptyBasketIcon /></div>
                <div className="basket-empty__text">В корзине пока еще ничего нет</div>
                <div className="basket-empty__button">
                    <button
                        type="button"
                        className="basket-total__btn_dark btn-transparent"
                        onClick={() => {
                            window.location.hash = "";
                        }}
                    >Вернуться в меню
                    </button>
                </div>
            </div>
        );
    }

    redirectToPayment(type) {
        setDeliveryMethod(type);
        this.props.onNavigateRequest("#/payment");
    }

    render() {
        const { showMobileAuth, pageTitle, min_amount_for_delivery, } = this.props;
        return (
            <main className={classNames("page basket-page", { "hide-element": showMobileAuth, })}>
                <Helmet>
                    <title>{pageTitle}</title>
                </Helmet>
                <div className="page__container container">
                    <p className="page__title">Ваш заказ</p>
                    <div className={classNames("basket-warning", { "hide-element": this.isDeliveryAvailable, })}>
                        <span className="basket-warning__icon">
                            <img src={warnIcon} alt="" />
                        </span>
                        <span className="basket-warning__text">
                            Минимальная сумма заказа {formatCurrency(min_amount_for_delivery)}. Вы можете заказать самовывоз или дополнить корзину.
                        </span>
                    </div>
                    <div className="basket">
                        {this.items}
                    </div>
                </div>
                <div className="container">
                    <Hits title="Рекомендуем" withScroll />
                    <div className="basket__total-block basket-total">
                        {this.props.cart.length ? this.basket : ""}
                        {this.props.cart.length ? this.buttons : ""}
                    </div>
                </div>
            </main>
        );
    }
}
BasketArea.propTypes = {
    showMobileAuth: PropTypes.bool.isRequired,
    pageTitle: PropTypes.string.isRequired,
    removeFromCart: PropTypes.func.isRequired,
    onNavigateRequest: PropTypes.func.isRequired,
    cart: PropTypes.instanceOf(Array).isRequired,
    min_amount_for_delivery: PropTypes.number.isRequired,
    removeAllItemsFromCart: PropTypes.func.isRequired,
    addToCart: PropTypes.func.isRequired,
};
export default BasketArea;
