import { GET_PROFILE, ERR } from "../AC";
import initState from "../store/initState";

function definitionsReducer(profile = initState.profile, { type, payload: data, }) {
    switch (type) {
    case GET_PROFILE: {
        return data.result;
    }
    case GET_PROFILE + ERR: {
        console.log(data);
        return profile;
    }
    default: {
        return profile;
    }
    }
}
export default definitionsReducer;
