import { GET_CATEGORIES, ERR } from "../AC";
import initState from "../store/initState";

function categoriesReducer(categories = initState.categories, { type, payload: data, }) {
    switch (type) {
    case GET_CATEGORIES: {
        return data.result.items;
    }
    case GET_CATEGORIES + ERR: {
        console.log(data);
        return categories;
    }
    default: {
        return categories;
    }
    }
}
export default categoriesReducer;
