import React from "react";
import { getRequest } from "../../helpers";
import BasketMobile from "../BasketMobile";
import MarketSection from "../MarketSection";
import Hits from "../Hits";

class Market extends React.Component {
    state = { categoriesData: [], };

    componentDidMount() {
        this.processingCategories();
    }

    async processingCategories() {
        const categoriesProcess = [];
        const { categories, } = this.props;

        if (categories) {
            for (let i = 0; i < categories.length; i++) {
                if (categories[i].published === 1) {
                    let productsProcess = [];
                    await getRequest(`/products/category?category_id=${categories[i].id}&page=1`)
                        .then((res) => {
                            productsProcess = res.data.result.items.slice();
                        });

                    categoriesProcess.push({
                        id: categories[i].id,
                        title: categories[i].title,
                        alias: categories[i].alias,
                        active: false,
                        products: productsProcess,
                    });
                }
            }

            this.setState({ categoriesData: categoriesProcess, });
        }
    }

    render() {
        return (
            <div className="container">
                <BasketMobile />

                <div className="market-sections">
                    <Hits title="Хиты" />
                    {this.state.categoriesData.map(category => (
                        <MarketSection
                            key={category.id}
                            category={category}
                        />
                    ))
                    }
                </div>
            </div>
        );
    }
}
Market.propTypes = { categories: PropTypes.instanceOf(Array).isRequired, };
export default Market;
