import React from "react";
import classNames from "classnames";
import MarketItem from "../MarketItem";

function MarketSection({ category, withScroll, }) {
    const items = category.products.map(product => (
        <MarketItem
            key={product.id}
            product={product}
            categoryID={category.id}
            withScroll={withScroll}
        />
    ));
    return (
        <section className="market-section" id={category.alias} name={category.alias}>
            <div className="market-section__title-container">
                <div className="market-section__title">{category.title}</div>
                <div className="market-section__title help-element" />
            </div>
            <div className={classNames({ "swipe-container": category.id === 0, })}>
                <div className={classNames("market-section__items", { "scroll-container": category.id === 0, })}>
                    {items}
                </div>
            </div>
        </section>
    );
}

MarketSection.propTypes = {
    category: PropTypes.shape({
        id: PropTypes.number.isRequired,
        title: PropTypes.string.isRequired,
        alias: PropTypes.string.isRequired,
        products: PropTypes.instanceOf(Object).isRequired,
    }).isRequired,
    withScroll: PropTypes.bool,
};
MarketSection.defaultProps = { withScroll: false, };
export default MarketSection;
